.c {
  height: 100vh;
  position: relative;
}
.c-bg {
  width: 20px;
  height: 100%;
  background-color: #59b256;
  position: absolute;
}
.c-wrapper {
  padding: 50px;
  display: flex;
}
.c-left {
  flex: 1;
}
.c-right {
  flex: 1;
  display: flex;
  align-content: center;
  justify-content: center;
  flex-direction: column;
}
.c-title{
    font-size: 60px;
    width: 80%;
}

.c-info-item{
display: flex;
align-items: center;
margin: 50px 0px;
font-weight: 300;
width: 70%;

}
.c-icon{
    width: 30px;
    height: 30px;
    margin-right: 20px;
}
.c-decs{
    font-weight: 200;
}
form{
    margin-top: 20px;
}
input{
    width: 50%;
    height: 50px;
    border: none;
    border-bottom: 1px solid #000;
    margin: 10px 0px;
    font-size: 16px;
    padding-left: 10px;
}
textarea{
    width: 100%;
    margin: 10px 0px;
    font-size: 16px;
    padding-left: 10px;
}
button{
    background-color: #59b256;
    color: white;
    padding: 15px;
    border: none;
    font-weight: 500;
    cursor: pointer;
}
@media screen and (max-width: 480px) {
  .c-wrapper {
    flex-direction: column;
    padding: 0px 50px;

  }
  .c-title{
    font-size: 30px;
  }
  .c-info-item{
    margin: 20px 0px;
    width: 100%;
  }
  .c-decs{
    display: none;
  }
}