.t {
  width: 50px;
  height: 25px;
  border-radius: 20px;
  border-radius: 1px solid #999;
  background-color: white;
  position: fixed;
  top: 10px;
  right: 10px;
  z-index: 999;
  display: flex;
  align-content: center;
  justify-content: space-around;
}
.t-icon {
  height: 15px;
  width: 15px;
}
.t-btn{
    height: 25px;
    width: 25px;
    border-radius: 50%;
    background-color: #999;
    position: absolute;
}